.about-container {
  font-family: Arial, sans-serif;
  color: #333;
  padding: 20px;
  max-width: 1200px;
  margin: auto;
}

.banner-section {
  position: relative;
  text-align: center;
}

.about-banner {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 10px;
}

.banner-title {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  color: #fff;
  background: rgba(0, 0, 0, 0.6);
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 2rem;
}

.about-card {
  background: #f9f9f9;
  margin: 20px 0;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.about-title, .about-subtitle {
  color: #0056b3;
}

.about-paragraph {
  line-height: 1.6;
}

.about-list {
  list-style: none;
  padding: 0;
}

.about-list li {
  margin: 8px 0;
  padding-left: 20px;
  position: relative;
}

.about-list li:before {
  content: 'â€¢';
  position: absolute;
  left: 0;
  color: #0056b3;
  font-weight: bold;
}

.trustees-section {
  display: flex;
  justify-content: space-around;
  gap: 20px;
  margin-top: 20px;
}

.trustee-card {
  width: 30%;
  text-align: center;
  background: #f9f9f9;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.trustee-image {
  width: 100%;
  height: auto;
  border-radius: 50%;
}

.trustee-name {
  font-size: 1.2rem;
  color: #0056b3;
  margin-top: 10px;
}

.trustee-description {
  font-size: 0.9rem;
  color: #666;
}
